html, body {
  overflow-x: hidden;
  overflow-y: auto;
  max-width: 100vw;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  background-color: #000000;
  color: #d1d5db;
  height: 100%;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 1.5rem;
  min-height: 100vh;
  background-color: #121212;
  color: #d1d5db;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  box-sizing: border-box;
  width: 100%;
  overflow-x: hidden;
}

.title {
  font-size: 2.5rem;
  font-weight: 700;
  margin: 0 0 2rem 0;
  text-align: center;
  color: #6366f1;
}

.layout {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.sidebar {
  background-color: #1f1f1f;
  padding: 1.5rem;
  border-radius: 1rem;
  box-shadow: 0 0 15px rgb(99 102 241 / 0.5);
  display: flex;
  flex-direction: column;
  gap: 1rem;
  max-height: none;
  overflow-y: visible;
  width: 75%;
  margin: 0 auto;
  box-sizing: border-box;
}

.sidebarTitle {
  font-size: 1.5rem;
  font-weight: 600;
  color: #818cf8;
  margin: 0 0 1rem 0;
  text-align: center;
}

.planButton {
  background-color: #374151;
  color: #d1d5db;
  padding: 0.75rem 1rem;
  border-radius: 0.5rem;
  text-align: left;
  border: none;
  cursor: pointer;
  font-weight: 600;
  transition: background-color 0.3s ease, color 0.3s ease;
  box-shadow: none;
}

.planButton:hover {
  background-color: #6366f1;
  color: white;
}

.planButtonActive {
  background-color: #4f46e5;
  color: white;
  box-shadow: none;
}

.noPlans {
  font-style: italic;
  color: #9ca3af;
  text-align: center;
}

.newPlanForm {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.input {
  padding: 0.75rem;
  border-radius: 0.5rem;
  background-color: #2a2a2a;
  border: 1.5px solid #6366f1;
  color: #d1d5db;
  font-size: 1rem;
  width: 100%;
  box-sizing: border-box;
}

.input:focus {
  outline: none;
  box-shadow: 0 0 5px #818cf8;
  border-color: #818cf8;
}

.buttonPrimary {
  background-color: #4f46e5;
  color: white;
  padding: 0.75rem;
  border-radius: 0.5rem;
  font-weight: 600;
  cursor: pointer;
  border: none;
  transition: background-color 0.3s ease;
}

.buttonPrimary:hover {
  background-color: #4338ca;
}

.buttonSecondary {
  background-color: #374151;
  color: #d1d5db;
  padding: 0.75rem;
  border-radius: 0.5rem;
  font-weight: 600;
  cursor: pointer;
  border: none;
  transition: background-color 0.3s ease;
}

.buttonSecondary:hover {
  background-color: #4b5563;
}

.mainPanel {
  background-color: #1f1f1f;
  padding: 1.5rem;
  border-radius: 1rem;
  box-shadow: 0 0 15px rgb(99 102 241 / 0.5);
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  width: 100%;
  box-sizing: border-box;
}

.planTitle {
  font-size: 2rem;
  font-weight: 600;
  color: #818cf8;
  margin: 0 0 1rem 0;
  text-align: center;
}

.daySection {
  background-color: #2a2a2a;
  padding: 1rem;
  border-radius: 0.75rem;
  box-shadow: inset 0 0 8px rgb(99 102 241 / 0.3);
  width: 100%;
  box-sizing: border-box;
}

.dayTitle {
  font-size: 1.25rem;
  font-weight: 600;
  color: #818cf8;
  margin: 0 0 0.75rem 0;
  border-bottom: 1px solid #6366f1;
  padding-bottom: 0.3rem;
  text-align: center;
}

.exerciseTable {
  width: 100%;
  border-collapse: collapse;
  margin-top: 1rem;
  border: 2px solid #6366f1;
  border-radius: 8px;
  overflow: hidden;
}

.exerciseTable th,
.exerciseTable td {
  padding: 0.75rem 1rem;
  text-align: center;
  border-right: 2px solid #6366f1;
  border-bottom: 2px solid #6366f1;
  vertical-align: middle;
}

.exerciseTable th:last-child,
.exerciseTable td:last-child {
  border-right: none;
}

.exerciseTable th {
  background-color: #1f1f1f;
  font-weight: 600;
  color: #818cf8;
}

.exerciseTable td {
  background-color: #2a2a2a;
}

.exerciseTable td:first-child {
  text-align: left;
  padding-left: 1.5rem;
}

.exerciseLink {
  color: #818cf8;
  text-decoration: none;
  font-weight: 500;
  white-space: nowrap;
}

.exerciseLink:hover {
  text-decoration: underline;
}

.actionsContainer {
  display: flex;
  justify-content: flex-end;
  min-width: 80px;
}

/* ...pozostałe style bez zmian... */

/* Przywrócone style przycisków Usuń/Edytuj */
.deleteButton {
  background-color: #ef4444;
  color: white;
}
.deleteButton.visible {
  display: flex; /* Pokazuje przycisk gdy jest klasa 'visible' */
}

.editButton, .deleteButton {
  padding: 0.5rem 1rem;
  border-radius: 0.25rem;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  white-space: nowrap; /* Zapobiega zawijaniu tekstu */
  font-size: 0.9rem;
  transform: translate(-27px);
}

.editButton {
  background-color: #3b82f6;
  color: white;
}

.editButton:hover {
  background-color: #2563eb;
}

.deleteButton:hover {
  background-color: #dc2626;
}

.planDeleteButton {
  background-color: #ef4444;
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 0.25rem;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-left: 0.5rem;
}

.planDeleteButton:hover {
  background-color: #dc2626;
}


/* Przywrócony styl przycisku Dodaj ćwiczenie */
.addExerciseButton {
  margin-top: 1.5rem;
  width: 100%;
  padding: 0.75rem;
  background-color: #4f46e5;
  color: white;
  font-weight: 600;
  border-radius: 0.5rem;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 0 10px rgba(129, 140, 248, 0.5);
  position: relative;
  overflow: hidden;
}

.addExerciseButton:hover {
  background-color: #4338ca;
  box-shadow: 0 0 15px rgba(129, 140, 248, 0.7);
}

.addExerciseButton::before {
  content: "+";
  position: absolute;
  left: 1rem;
  top: 50%;
  transform: translateY(-50%);
  font-size: 1.2rem;
}

/* Poprawiony formularz dodawania ćwiczeń */
.modalInput {
  padding: 0.75rem;
  border-radius: 0.5rem;
  background-color: #2a2a2a;
  border: 1.5px solid #6366f1;
  color: #d1d5db;
  font-size: 1rem;
  width: 100%;
  box-sizing: border-box;
  transition: all 0.3s ease;
}

.modalInput:focus {
  outline: none;
  box-shadow: 0 0 8px rgba(129, 140, 248, 0.5);
  border-color: #818cf8;
}

.modalButtons {
  display: flex;
  gap: 1rem;
  margin-top: 1.5rem;
}

.modalButtons button {
  flex: 1;
}

/* Animacje */
@keyframes buttonEntry {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.addExerciseButton {
  animation: buttonEntry 0.5s ease-out;
}

@media (max-width: 768px) {
  .editButton,
  .deleteButton,
  .planDeleteButton {
    padding: 0.4rem 0.8rem;
    font-size: 0.9rem;
  }
  

  .title {
    font-size: 2rem;
  }

  .layout {
    gap: 1.5rem;
  }

  .sidebar {
    padding: 1rem;
    border-radius: 0.5rem;
    width: 100%;
  }

  .sidebarTitle {
    font-size: 1.25rem;
  }

  .planButton {
    padding: 0.5rem 0.75rem;
    font-size: 0.9rem;
  }

  .planTitle {
    font-size: 1.5rem;
  }

  .dayTitle {
    font-size: 1rem;
  }

  .exerciseTable {
    display: block;
    overflow-x: auto;
  }

  .exerciseTable th,
  .exerciseTable td {
    padding: 0.5rem 0.75rem;
    min-width: 120px;
    font-size: 0.9rem;
  }

  .deleteButton {
  background-color: #ef4444;
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 0.25rem;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  display: none; /* Domyślnie ukryty */
}

  .modalContainer {
    max-width: 90%;
    padding: 1.5rem;
  }

  .modalTitle {
    font-size: 1.25rem;
  }

  .modalSelect, .modalInput {
    font-size: 0.9rem;
    padding: 0.5rem;
  }
}

@media (max-width: 480px) {
  .editButton, .deleteButton {
    padding: 0.4rem 0.8rem;
    font-size: 0.8rem;
  }
  
  .exerciseTable td:last-child {
    min-width: 90px;
  }


  .sidebarTitle {
    font-size: 1rem;
  }

  .planButton {
    font-size: 0.85rem;
  }

  .daySection {
    padding: 0.75rem;
  }

  .exerciseTable th,
  .exerciseTable td {
    padding: 0.4rem 0.6rem;
    font-size: 0.8rem;
    min-width: 100px;
  }

  .deleteButton,
  .editButton {
    font-size: 0.8rem;
  }

  .exerciseTable td:first-child {
    padding-left: 1rem;
  }
  
}